.body{
  background-color:#fff;
}

.crest{
  cursor: pointer;
}

.selected-crest{
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: default;
  margin-right:-80px;
  display: -ms-flexbox;
  display: flex;
  width: 200px;
  height: 200px;
  margin-top: 40px;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -ms-flex-align: stretch;
  align-items: stretch;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: perspective(1413px) rotate(0deg);
  transform: perspective(1413px) rotate(0deg);
  -webkit-transform-origin: 50% 100%;
  -ms-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  -webkit-transition: all 400ms ease;
  -o-transition: all 400ms ease;
  transition: all 400ms ease;
  font-size: 11px;
  line-height: 19px;
}

.selected-crest:hover{
  background-color: transparent;
}

.div-block-6{
  background-color: none;
  cursor: pointer;
}

@media (max-width: 479px) {
  .selected-crest {
    width: 100px;
    height: 100px;
    margin-top: 13px;
    margin-bottom: 13px;

    margin-right:-65px;
  }
}


