footer.footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3em;
  margin-bottom: 3em;
  width: calc(100% - 20px);
}

.footer-nav {
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #d9d9d9;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 2em;
  padding: 0em 0em 2em;
}

.footer-nav .footer-nav-primary {
  display: -ms-flexbox;
  display: flex;
}

.footer-nav .footer-nav-social {
  display: -ms-flexbox;
  display: flex;
  margin-left: auto;
}

.footer-nav .footer-nav-social .footer-nav-social-icon {
  display: block;
  margin-left: 20px;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.footer-nav .footer-nav-social .footer-nav-social-icon img{
  width: 30px;
}

.footer-nav .footer-nav-social .footer-nav-social-icon:hover{
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
}

.footer-app-store-links {
  text-align: center;
}

.footer-app-store-links .footer-app-store-logo {
  display: inline-block;
}

.footer-app-store-links .footer-app-store-logo img {
  max-width: 150px;
  margin-right: 20px;
  width: 100%;
}

.footer-text-link {
  color: #28323F;
  display: inline-block;
  font-family: Poppins;
  font-size: 18px;
  font-weight: bold;
  margin-right: 1em;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.footer-text-link:hover {
  text-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
  color: #28323F;
  text-decoration: none;
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
}

@media screen and (max-width: 767px) {
  footer.footer {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
    max-width: 479px;
    padding: 1em 10px;
    margin: 0 auto;
  }

  .footer-nav {
    border-bottom: 0px;
    padding-top: 2em;
    margin-bottom: 0em;
  }

  .footer-app-store-links {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 20px 0 0;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }

  .footer-app-store-links .footer-app-store-logo {
    margin-bottom: 10px;
    min-width: 200px;
    padding: 2%;
    width: 46%;
  }

  .footer-app-store-links .footer-app-store-logo img {
    max-width: none;
    width: 100%;
    margin: 0;
  }
}



