.match {
    max-width: 479px;
    padding-bottom: 0;
    border-right: 0 none #e7e7e7;
    -webkit-box-shadow: 0 5px 10px 0 rgba(0,0,0,0.08);
            box-shadow: 0 5px 10px 0 rgba(0,0,0,0.08);
    border-left: 0 none #e7e7e7;
    margin-right: auto;
    margin-left: auto;
    background-color: #fff;
    -webkit-transition: max-height 3.5s ease-in-out;
    -o-transition: max-height 3.5s ease-in-out;
    transition: max-height 3.5s ease-in-out;
}

  .match.has-expander {
    cursor: pointer;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
  }

  .match.matchcomplete {
    background-color: #f5f5f5;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  .match.has-expander:hover, .match.expander-open {
    background-color: #fff;
    -webkit-box-shadow: 0 5px 10px 0 rgba(0,0,0,0.08);
            box-shadow: 0 5px 10px 0 rgba(0,0,0,0.08);
    -webkit-transform: translateY(-2px);
        -ms-transform: translateY(-2px);
            transform: translateY(-2px);
  }
  .numberbg {

    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 42px;
    height: 45px;
  }
  .numberplace {
    position: relative;
    padding-left: 7px;
    padding-top: 5px;
    font-family: Poppins,sans-serif;
    color: #fff;
    font-size: 16px;
    /*line-height: 26px;*/
    font-weight: 600;
  }
  .homecrest {
    position: static;
    left: 40px;
    top: -20px;
    width: 40px;
    height: 40px;
    background-color: transparent;
    background-position: 50% 50%;
    background-size: contain;
    background-repeat: no-repeat;
  }
  .homecrest.incard {
    margin: 15px auto 8px;
    padding-top: 0px;
  }
  .crestcontainer {
    position: relative;
    left: 4%;
    top: -14px;
    width: 40px;
    float: left;
  }
  .vs {
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 6px;
    padding-bottom: 6px;
    font-family: Poppins, sans-serif;
    font-size: 10px;
    text-align: center;
    letter-spacing: 1px;
  }
  .awaycrest {
    width: 40px;
    height: 40px;
    background-color: transparent;
    background-position: 50% 50%;
    background-size: contain;
    background-repeat: no-repeat;
  }
  .infocontainer {
    position: relative;
    left: 12%;
    top: -14px;
    width: 79%;
    height: auto;
    padding-right: 26px;
    float: left;
  }
  .datetime {
    font-family: Poppins, sans-serif;
    color: #000;
    font-size: 10px;
    font-weight: 500;
    letter-spacing: 2px;
  }
  .narrative {
    font-family: Poppins, sans-serif;
    font-size: 12px;
    font-weight: 300;
    line-height: 18px;
    overflow: hidden;
    padding-top: 7px;
    position: relative;
  }
.innercontainer{
      height: 143px;
}
  .node {
    /* height: 200px; */
    width: 100%;
    background: red;
  }
  .node.firstNode {
    background: blue;
    /* height: 250px; */
  }
    .narrative:after {
      background: blue;
      bottom: 0;
      content: '';
      /* height: 12px; */
      left: 0;
      position: absolute;
      width: 100%;
    }
    .match.matchcomplete .narrative:after {
      background: red;
    }
    .match.matchscheduled .narrative:after {
      background: orange;
    }
  .narrative.livenarrative {
    margin-bottom: 16px;
  }
  .narrative.livenarrative.livenarrativecomplete {
    color: #999;
  }
  .expanderclosed {
    position: relative;
    margin-bottom: 5px;
    left: 0px;
    top: -8px;
    right: 0px;
    bottom: 0px;
    width: 100%;
    height: 21px;
    margin-right: auto;
    margin-left: auto;
    clear: left;
    background-position: 50% 50%;
    background-size: auto;
    background-repeat: no-repeat;
    cursor: pointer;
  }

  .expanderopen {
    position: relative;
    margin-bottom: 5px;
    left: 0px;
    top: -8px;
    right: 0px;
    bottom: 0px;
    width: 100%;
    height: 21px;
    margin-right: auto;
    margin-left: auto;
    clear: left;
    background-position: 50% 50%;
    background-size: auto;
    background-repeat: no-repeat;
    cursor: pointer;
  }
