  .clear{
    position: relative;
    margin-bottom: 5px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    width: 100%;
    height: 21px;
    margin-right: auto;
    margin-left: auto;
    clear: left;
  }
  .teamnametext {
    font-weight: 600;
  }
  .contentcontainer {
    width: 90%;
    margin-right: auto;
    margin-left: auto;
    text-align: left;
  }
  .livescore {
    position: relative;
    top: -13px;
    display: -ms-flexbox;
    display: flex;
    width: 84%;
    margin-right: auto;
    margin-left: auto;
    -ms-flex-align: center;
    align-items: center;
    border-style: none;
  }
  .scoreformatting {
    display: inline-block;
    width: 20%;
    padding-right: 0px;
    padding-left: 0px;
    padding-top: 5px;
    border: 1px none #000;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-weight: 700;
    text-align: center;
  }
  .scoreformatting.scoretime {
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
  }
  .scoreformatting.scoretime.in-progress {
    padding-top: 15px;
  }

  @media screen and (max-width: 767px) {
    .match{
      padding-bottom: 0;
      border-right: 0 none #e7e7e7;
      border-bottom: 1px solid #e7e7e7;
      border-left: 0 none #e7e7e7;
    }
    .narrative{
      height: 100% !important;
    }

  }
  @media screen and (max-width: 479px) {
    .match {

    }
    .match.has-expander:hover, .match.expander-open {
      background-color: #fff;
      -webkit-box-shadow: none;
              box-shadow: none;
      -webkit-transform: none;
          -ms-transform: none;
              transform: none;
    }
    .match-container:last-of-type .match:hover {
      -webkit-box-shadow: 0 5px 10px 0 rgba(0,0,0,0.08);
              box-shadow: 0 5px 10px 0 rgba(0,0,0,0.08);
    }
  }



